import keyboardPackage from '../../components/keyboard-package/keyboard-package.vue';
import passwordInput from '../../components/password-input/password-input.vue';
export default {
    data() {
        return {
            bar: '',
            moder: '',
            fee: '',
            fil_num: '',
            password: '',
            numberList: [],
            length: 6,
            type: 'number',
            passIn: false,
            wallet_value: ''
        };
    },
    components: {
        keyboardPackage,
        passwordInput
    },
    onLoad(res) {
        this.bar = res.bar;
        this.fee = res.fee;
        this.wallet_value = res.wallet_value;
    },
    onBackPress(option) {
        plus.key.hideSoftKeybord();
    },
    methods: {
        getFilNum: function(e) {
            this.fil_num = e.detail.value;
        },
        getTranferAdd: function(e) {
            this.wallet_value = e.detail.value;
        },
        link() {
            var that = this;
            uni.request({
                url: this.url + 'walletaddress/',
                method: 'GET',
                header: {
                    Authorization: 'JWT' + ' ' + this.global_.token
                },
                success(res) {
                    if (res.statusCode == 400) {
                        uni.showToast({
                            title: '用户未实名认证',
                            icon: 'none',
                            duration: 2000
                        });
                    }
                    if (res.statusCode == 201) {
                        uni.showToast({
                            title: '身份认证审核中，请等待',
                            icon: 'none',
                            duration: 2000
                        });
                    }
                    if (res.statusCode == 200) {
                        uni.navigateTo({
                            url: '../choose-address/choose-address?bar=' + that.bar + '&fee=' + that.fee
                        });
                    }
                    if (res.statusCode == 302) {
                        uni.showToast({
                            title: '用户未设置资金密码',
                            icon: 'none',
                            duration: 2000
                        });
                    }
                }
            });
        },
        fusre() {
            this.fil_num = this.bar;
        },
        clo: function() {
            this.passIn = false;
            this.$refs['number'].close();
            this.numberList.length = 0;
        },
        onChange(e) {
            if (e.show == false) {
                this.passIn = false;
            }
            this.numberList.length = 0;
        },
        onDelete() {
            this.numberList.pop();
        },
        onConfirm() {
            if (this.numberList.length != 6) {
                uni.showToast({
                    title: '请输入六位交易密码！',
                    duration: 2000,
                    icon: 'none'
                });
            }
        },
        onInput(val) {
            var that = this;
            that.numberList.push(val);
            that.password = that.numberList.join().replace(/,/g, '');
            if (that.numberList.length >= that.length) {
                uni.request({
                    url: that.url + 'assets/capitalcode/',
                    method: 'POST',
                    data: {
                        capital_code: that.password
                    },
                    header: {
                        Authorization: 'JWT' + ' ' + that.global_.token
                    },
                    success(res) {
                        if (res.statusCode == 400) {
                            that.numberList.pop();
                            that.numberList.length = 0;
                            that.$refs.wrong.flag = false;
                            var n = res.data.data.err_num;
                            var s = 5 - n;
                            that.$refs.wrong.tip = '剩余' + s + '次机会';
                        }
                        if (res.statusCode == 423) {
                            that.passIn = false;
                            that.$refs['number'].close();
                            uni.showToast({
                                title: '交易密码已锁定,请在今日24:00后进行交易',
                                icon: 'none',
                                duration: 2000
                            });
                        }
                        if (res.statusCode == 201) {
                            that.passIn = false;
                            that.$refs['number'].close();
                            uni.redirectTo({
                                url: '../commit/commit?fil_num=' + that.fil_num + '&wallet_value=' + that.wallet_value
                            });
                        }
                        var page = getCurrentPages().pop();
                        if (page == undefined || page == null) return;
                        page.onLoad();
                    }
                });
                this.numberList.length = 0;
            }
        },
        save: function() {
            var that = this;
            var b = parseFloat(that.bar);
            var f = parseFloat(that.fil_num);
            if (that.wallet_value == '') {
                uni.showToast({
                    title: '转账地址不能为空',
                    icon: 'none',
                    duration: 2000
                });
                return false;
            }
            if (that.fil_num == '') {
                uni.showToast({
                    title: '提币数量不能为空',
                    icon: 'none',
                    duration: 2000
                });
                return false;
            }
            if (that.fil_num < 0.001) {
                uni.showToast({
                    title: '提币数量无效',
                    icon: 'none',
                    duration: 2000
                });
                return false;
            }
            if (f > b) {
                uni.showToast({
                    title: '提币数量余额不足',
                    icon: 'none',
                    duration: 2000
                });
                return false;
            }
            uni.request({
                url: that.url + 'assets/transfer/',
                method: 'POST',
                data: {
                    fil_num: that.fil_num,
                    address: that.wallet_value
                },
                header: {
                    Authorization: 'JWT' + ' ' + this.global_.token
                },
                success(res) {
                    if (res.statusCode == 411) {
                        uni.showToast({
                            title: '未进行实名认证',
                            icon: 'none',
                            duration: 2000
                        });
                        return false;
                    }
                    if (res.statusCode == 412) {
                        uni.showToast({
                            title: '实名认证中，请耐心等待',
                            icon: 'none',
                            duration: 2000
                        });
                        return false;
                    }
                    if (res.statusCode == 403) {
                        uni.showToast({
                            title: '资金密码未设置',
                            icon: 'none',
                            duration: 2000
                        });
                        return false;
                    }
                    if (res.statusCode == 400) {
                        uni.showToast({
                            title: '输入信息有误(提币地址最少32位)',
                            icon: 'none',
                            duration: 2000
                        });
                        return false;
                    }
                    if (res.statusCode == 423) {
                        uni.showToast({
                            title: '交易密码已锁定,请在今日24:00后进行交易',
                            icon: 'none',
                            duration: 2000
                        });
                        return false;
                    }
                    if (res.statusCode == 200) {
                        that.passIn = true;
                        that.$refs['number'].open();
                        that.onInput(val);
                    }
                }
            });
        }
    }
};
